<template>
  <TableForm ref="tf_user" :formDetails="formDetails" :columnList="columnList" @processData="insertUser" />
</template>

<script>
import UserService from '../../services/UserService'
import TableForm from '../custom/TableForm'
export default {
  name: 'User',
  components:{
    TableForm
  },
  data () {
    return {
      formDetails:{
        title: ((window.location.href).includes("create")) ? 'Create user' : 'Edit user',
        btnSubmitText: ((window.location.href).includes("create")) ? 'Create' : 'Update',
        submitUrl: '/api/user/register'
      },
      columnList:{
        name:{
          type: 'input',
          datatype: 'text',
          label: 'Name',
          prepend: '@',
          append: '',
          description: 'This is username',
          dbColumn: 'name',
          disabled: false
        },
        email:{
          type: 'input',
          datatype: 'email',
          label: 'Email',
          prepend: '',
          append: '',
          description: 'This is email',
          dbColumn: 'email',
          disabled: false
        },
        password:{
          type: 'input',
          datatype: 'password',
          label: 'Password',
          prepend: '',
          append: '',
          description: 'This is password',
          dbColumn: 'password',
          disabled: false
        },
      },
      usersOpened: null
    }
  },
  methods: {
    async insertUser(formData){
      try{
        let InsertUserDatas = UserService.insertUser(formData);
        InsertUserDatas.then( (res) =>{
          this.$refs.tf_user.showSuccessAlert('Success');
        })
        .catch((error) =>{
            this.$refs.tf_user.showErrorAlert(error.response.data);
        });
      }catch(error) {
        console.log(error);
      };
    },
    goBack() {
      this.usersOpened ? this.$router.go(-1) : this.$router.push({path: '/users'})
    }
  }
}
</script>
