<template>
  <transition name="fade">
    <CCard>
      <CCardHeader>
        <CIcon name="cil-pencil"/> {{this.formDetails.title}}
        <div class="card-header-actions">
          <CLink
            class="card-header-action btn-minimize"
            @click="formCollapsed=!formCollapsed"
          >
            <CIcon :name="`cil-chevron-${formCollapsed ? 'bottom' : 'top'}`"/>
          </CLink>
        </div>
      </CCardHeader>
      <CCollapse :show="formCollapsed">
        <CCardBody>
          <CAlert
            color="success"
            closeButton
            :show.sync="successShow"
          >
            {{successMsg}}
          </CAlert>

          <CAlert :show.sync="errorShow" color="danger">
            {{errorMsg}}
          </CAlert>
          <CForm v-on:submit.prevent="formSubmit">
            <div v-for="column in columnList" :key="column.id">
              <CInput v-if="column.type == 'input'"
                :label="column.label"
                :description="column.description"
                :type="column.datatype"
                :prepend="column.prepend"
                :append="column.append"
                :disabled="column.disabled"
                v-model="formData[column.dbColumn]"
              />
            </div>
            <div class="form-actions">
              <CButton type="submit" color="primary">{{this.formDetails.btnSubmitText}}</CButton>
            </div>
          </CForm>
        </CCardBody>
      </CCollapse>
    </CCard>
  </transition>
</template>

<script>
export default {
  name: 'TableForm',
  props:{
    formDetails: Object,
    columnList: Object,
  },
  data () {
    return {
      formCollapsed: true,
      formData:{},
      successShow: false,
      successMsg: '',
      errorShow: false,
      errorMsg: '',
    }
  },
  computed: {},
  methods: {
    formSubmit(){
      // Reset all alert before submit
      this.successShow = false;
      this.successMsg = '';
      this.errorShow = false;
      this.errorMsg = '';
      this.$emit('processData', this.formData);
    },
    showSuccessAlert(msg){
      this.successShow = true;
      this.successMsg = msg;
    },
    showErrorAlert(msg){
      this.errorShow = true;
      this.errorMsg = msg;
    },
    goBack() {
      this.usersOpened ? this.$router.go(-1) : this.$router.push({path: '/users'})
    }
  }
}
</script>
